body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h3  {
  margin: 0px;
}

.App {
  display: flex;
  justify-content: space-around;
  max-width: 1290px;
  margin: 0 auto;
  color: black;
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

.control-container {
  flex-grow: 1;
  max-width: 50%;
}

.current-mode {
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: capitalize;
  font-weight: 500;
  font-size: 1.2em;
  margin-top: 10px;
  margin-bottom: 0px;
}

@media only screen and (max-width: 768px) {
  .App {
    flex-direction: column;
  }
  .control-container {
    width: 100%;
    max-width: 100%;
  }
}

input, button {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}
.cell {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  width: 20px;
  margin: 1px;
  border-radius: 3px;
  cursor: pointer;
  border: none;
}
.titled-container {
  position: relative;
  border: 3px solid;
  padding: 10px;
  margin-top: 20px;
  min-height: 50px;
  min-width: 200px;
  display: inline-block;
}

.container-title {
  position: absolute;
  top: -13px;
  left: 5px;
  margin: 0px;
  font-weight: 500;
  padding: 0 10px;
  background-color: white;
  font-size: 1.2rem;
  line-height: 1.2rem;
}

@media only screen and (max-width: 768px) {
  .titled-container {
    display: block;
  }
}

.styled-input {
  display: inline-flex;
  align-items: center;
  margin-right: 10px;
}

.input-title {
  margin-right: 5px;  
}

.styled-input input {
  font-size: 1rem;
  width: auto;
  border-style: solid;
  border-radius: 3px;
  border-width: 2px;
  padding-left: 5px;
}

.styled-button {
  border: none;
  background-color: transparent;
  transition: background-color 0.1s ease-in;
  border-bottom: 2px solid black;
  padding:5px;
  margin-right:5px;
  font-size: 1rem;
  font-weight: 500;
}

.styled-button:hover {
  cursor: pointer;
  background-color: #bdb8b8;
}
.colour-cell {
  display: inline-block;
  height: 36px;
  width: 36px;
}
.snake-control-container {
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.vertical-flex-container {
  display: inline-flex;
  flex-direction: column;
}
